<template>
    <div :class="animateClass">
        <Head title="资讯" :show="true"></Head>
        <div v-html="page"></div>
    </div>

</template>
<script>
import {
  animate
} from "@/utils/index.js"
export default {
    mixins:[animate],
    data(){
        return {
            page:""
        }
    },
    mounted(){
        this.$axios.get("/maizuo/gateway?actId=ElzMZU125260",{
            headers:{
                "X-Host": "mall.act.static-page.info"
            }
        }).then(res=>{
            // console.log(res)
            this.page = res.data.data.data.content;//爬取页面  content 为HTML5 代码
            console.log(this.page)
        })
    }
}
</script>
